"use client";
import { useState } from "react";
import { ChevronDown } from "lucide-react";
import Navbar from "./Navbar";
import Link from "next/link";
import { cn } from "@/lib/utils";
import ProductCard from "./ProductCard"; // Common UI for products

const categories = [
  { name: "Dresses", subcategories: ["Casual", "Party", "Maxi"] },
  { name: "Shirts", subcategories: ["T-Shirts", "Formal", "Casual"] },
];

const sampleProducts = [
  {
    id: "1",
    image: "/1.jpg",
    name: "Casual Dress",
    price: "49.99",
    description: "A beautiful casual dress",
    category: "dresses",
  },
  {
    id: "2",
    image: "/1.jpg",
    name: "Formal Shirt",
    price: "39.99",
    description: "Perfect for office",
    category: "shirts",
  },
];

export default function Shop() {
  const [expandedCategories, setExpandedCategories] = useState<number[]>([]);

  const toggleCategory = (index: number) => {
    setExpandedCategories((prev) =>
      prev.includes(index) ? prev.filter((i) => i !== index) : [...prev, index]
    );
  };

  return (
    <div className="min-h-screen">
      <Navbar />
      <div className="container mx-auto px-4 flex">
        {/* Sidebar */}
        <div className="w-64 pr-6">
          <ul className="border rounded-lg overflow-hidden">
            {categories.map((category, index) => (
              <li key={category.name} className="relative">
                <div className="flex flex-col">
                  <div
                    className="flex justify-between px-4 py-2 cursor-pointer border-b last:border-b-0"
                    onClick={() => toggleCategory(index)}
                  >
                    <Link href={`/shop/${category.name.toLowerCase()}`}>
                      {category.name}
                    </Link>
                    <ChevronDown className={cn("h-4 w-4 transition-transform", expandedCategories.includes(index) ? "rotate-180" : "")} />
                  </div>

                  {expandedCategories.includes(index) && (
                    <div className="bg-gray-50">
                      {category.subcategories.map((sub) => (
                        <Link
                          key={sub}
                          href={`/shop/${category.name.toLowerCase()}/${sub.toLowerCase()}`}
                          className="block px-6 py-2 hover:bg-primary hover:text-white text-sm border-t"
                        >
                          {sub}
                        </Link>
                      ))}
                    </div>
                  )}
                </div>
              </li>
            ))}
          </ul>
        </div>

        {/* Products Display */}
        <div className="flex-1 grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-4">
          {sampleProducts.map((product) => (
            <ProductCard key={product.id} product={product} />
          ))}
        </div>
      </div>
    </div>
  );
}
